@import '../../../base';

.overview-container {
  @media screen and (max-width: 560px) {
    padding: 50px auto;
  }

  .desc-width {
    width: 40%;
  }

  .border-left {
    border-left: solid 1px $color-line;  
  }

  .overview {
    display: flex;
    padding-left: 36px;
    // padding-bottom: 28px;
    align-items: flex-end;
    border-right: solid 1px $color-line;

    .overview-text {
      font-size: $font-size-extra-big;
      font-weight: $font-weight-heavy;
      color: $color-title;
    }
  }

  .edit-form {
    align-items: flex-start;
    border: none;
    padding-left: 10px;
    
    @media screen and (max-width: 576px) {
      width: 100%;
      padding: 0;
    }

    .edit-detail-container {
      margin-top: 15px;
    }
  }

  .category-resp-fixed-container,
  .category-resp-container {
    display: flex;
    flex-grow: 1;
    width: 60%;
  }
  
  .category-container-bottom {
    display: flex;
    border-right: solid 1px $color-line;
    border-left: solid 1px $color-line;

    .bottom-overview {
      border-right: none;
    }

    .bottom-border {
      border-bottom: solid 1px $color-line;
    }

    .bottom-cost {
      font-size: $font-size-big * 1.3;
      font-weight: $font-weight-heavy;
      color: $color-title;
      margin: 0;
      margin-top: 10px;

      @media screen and (max-width: 560px) {
        font-size: $font-size-regular;
      }
    }

    .extra-info,
    .bottom-discount {
      min-height: 25px;
      font-size: $font-size-regular;
      font-weight: $font-weight-normal;
      color: $color-caption;
      margin: 0;
      margin-top: -10px;
      margin-bottom: 10px;
      text-decoration: line-through;

      @media screen and (max-width: 560px) {
        font-size: $font-size-smallest;
        margin-top: -5px;
        margin-bottom: 0;
      }
    }

    .extra-info {
      margin-bottom: 0;
      text-decoration: none !important;
    }
  }

  .top-header {
    .bottom-discount {
      min-height: 25px;
      font-size: $font-size-regular;
      font-weight: $font-weight-normal;
      color: $color-caption;
      margin: 0;
      margin-top: -15px;
      text-decoration: line-through;

      @media screen and (max-width: 560px) {
        min-height: 22px;
        font-size: $font-size-smallest;
      }
    }
  }

  .top-header {
    .bottom-discount {
      min-height: 25px;
      font-size: $font-size-regular;
      font-weight: $font-weight-normal;
      color: $color-caption;
      margin: 0;
      margin-top: -15px;
      text-decoration: line-through;

      @media screen and (max-width: 560px) {
        min-height: 22px;
        font-size: $font-size-smallest;
      }
    }
  }

  .na {
    display: flex;
    justify-content: center;
    .na-text {
      font-size: $font-size-small;
      font-weight: $font-weight-bold;
      color: $color-body;
    }
  }
  
  .category-resp-fixed-hidden-container {
    height: 0;
    display: none;
  }
  
  .category-resp-fixed-container {
    height: 0;
    display: none;
  }

  // @media screen and (max-width: 560px) {
    .category-resp-fixed-container,
    .category-resp-fixed-bottom-container  {
      height: auto;
      display: flex;
      position: fixed;
      z-index: 100;
      background-color: white;
      // width: calc(100% - 30px);
      width: 1006px;
      // left: 0;
      // margin: 0 15px;
      max-width: calc(100% - 30px);
    }

    .category-resp-fixed-container {
      top: $navbar-resp-top-height;
      border: solid 1px $color-line;
      z-index: 999;
    }

    .category-resp-fixed-bottom-container {
      bottom: 0;
      z-index: 200;
      .bottom-border {
        border-bottom: none;
      }
    }
  // }

  .line-border {
    border-left: solid 1px $color-line;

    @media screen and (max-width: 768px) {
      &:nth-of-type(1) {
        border-left: none;
      }
    }
  }

  .category-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    // padding: 21px 0;

    @media screen and (max-width: 768px) {
      padding: 0;
    }

    .category {
      font-size: $font-size-big;
      font-weight: $font-weight-semibold;
      color: $color-title;
      margin: 0;
      line-height: 18px;
    }

    .sub-category {
      font-size: $font-size-regular;
      font-weight: $font-weight-light;
      color: $color-body;
      margin: 0;

      @media screen and (max-width: 560px) {
        font-size: $font-size-smallest;
      }
    }

    .cost {
      font-size: $font-size-big;
      font-weight: $font-weight-heavy;
      color: $color-title;
      margin-top: 15px;
      margin-bottom: 10px;
    }

    .cashback {
      margin-top: 8px;
      font-size: $font-size-smallest;
      font-weight: $font-weight-heavy;
      color: $color-hover;
      text-align: center;
    }

    .cashback-link {
      &:hover {
        text-decoration-line: underline;
      }
    }

    @media screen and (max-width: 560px) {
      .category {
        font-size: $font-size-regular;
        font-weight: $font-weight-semibold;
        color: $color-title;
        margin: 0;
        line-height: 18px;
      }

      .limit {
        font-size: $font-size-smallest;
        font-weight: $font-weight-light;
        color: $color-body;
        margin: 0;
      }

      .cost {
        font-size: $font-size-regular;
        font-weight: $font-weight-heavy;
        color: $color-title;
        margin-top: 15px;
        margin-bottom: 10px;
      }

      .cashback {
        max-width: 60%;
        font-size: 10px;
      }
    }

    .buy-button {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 100%;
      padding: 10px 20px;

      .button-medium-color {
        min-width: auto !important;
        width: 100%;
        height: 48px;

        p {
          font-size: $font-size-regular !important;
        }
      }

      @media screen and (max-width: 800px) {
        .button-medium-color p {
          font-size: $font-size-small !important;
        }
      }

      @media screen and (max-width: 560px) {
        padding: 0 10px;
        .button-medium-color {
          height: 36px !important;
      
          p {
            font-size: 10px !important;
          }
        }
      }
    }
  }

  .product-changable-category {
    position: relative;
    border: solid 1px $color-line;
    border: solid 1px $color-line;
    margin-right: -1px;
    margin-bottom: 23px;
    padding-top: 40px;
    display: flex;
    align-items: center;

    @media screen and (min-width: 576px) {
      padding-bottom: 35px;
    }

    .edit-container {
      position: absolute;
      top: 8px;
      right: 8px;
      max-width: 120px;
      height: 30px;
      display:flex;
      justify-content: center;
      align-items: center;
      background-color: $color-light-green;
      border: $color-light-grey 1px solid;
      border-radius: 35px;
      padding: 0 10px;
      color: $color-body;
      font-size: $font-size-smallest * 0.7;
      font-weight: $font-weight-semibold;

      @media screen and (min-width: 650px) {
        font-size: $font-size-smallest;
      }

      img {
        width: 30px;
        height: 30px;
        // filter: $color-filter-hover;
  
        &:hover {
          box-shadow: 0 6px 8px 0 rgba(0, 0, 0, 0.1);
          // filter: brightness(80%);;
        }
  
        &:active {
          width: 30px;
          height: 30px;
        }
      }
    }

    .product-text {
      padding: 0 10px;
      text-align: center;
      font-size: $font-size-smallest * 0.9;
      font-weight: $font-weight-semibold;
      color: $color-title;
      flex: 1;
      word-break: break-all;
    }

    .discount {
      width: 100%;
      @media screen and (min-width: 576px) {
        position: absolute;
        left: -10px;
        bottom: 10px;
        width: auto;
        padding: 2px 10px;
      }

      min-height: 30px;
      border-radius: 4px;
      background-image: linear-gradient(259deg, #ffdf00, #ff9f0b);
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;

      p {
        font-size: $font-size-smallest;
        font-weight: $font-weight-semibold;
        color: white;
        margin: 0;
      }
    }
  }

  .no-discount {
    @media screen and (min-width: 576px) {
      padding-bottom: 10px;
    }
  }

  .insurer-container {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 150px;
    max-width: 100%;
    height: 70px;
    padding: 0 10px;

    @media screen and (min-width: 576px) {
      width: 100px;
    }

    img {
      max-width: 100%;
      max-height: 100%;
    }
  }

  .detail-container {
    background-color: rgba(255, 255, 255, 0.5);
    box-shadow: 0 30px 40px 0 rgba(133, 195, 255, 0.25);
  }

  .detail-not-changable-container {
    margin-bottom: 48px;
  }

  .card {
    border: none;

    .card-body {
      padding: 0;
    }
  }

  .info-accordian-container,
  .info-container {
    width: 100%;
    
    .desc-accordion-container {
      text-align: left;
      padding: 30px;
      display: flex;
      justify-content: flex-start;
      font-size: $font-size-smallest;
      border: solid 1px $color-line;
      border-top: none;
      border-bottom: none;
      
      @media screen and (max-width: 768px) {
        padding: 20px;
      }
      p {
        margin: 0;
      }
      .title {
        color: $color-title;
      }
      .desc {
        color: $color-caption;
      }
      
      .arrow-container {
        flex: 1;
        display: flex;
        justify-content: flex-end;
        img {
          filter: brightness(0) saturate(100%) $color-filter-hover;
        }
      }
    }

    .show {
      border-top: solid 2px $color-line;
      border-bottom: solid 3px $color-line;
    }

    .info-container-border {
      border-left: solid 1px $color-line;
      border-right: solid 1px $color-line;
    }

    .desc-resp-container {
      flex-direction: column;
      padding: 10px 20px 0 20px;
      color: white;
      font-size: $font-size-smallest;
      

      .title {
        font-size: $font-size-tiny;
        font-weight: $font-weight-semibold;
        color: $color-title;
        margin: 0;
      }
      .desc {
        font-size: $font-size-tiny;
        font-weight: $font-weight-normal;
        color: $color-caption;
        margin: 0;
      }
      .title-desc {
        font-size: $font-size-tiny * 0.9;
      }
    }

    .item-container {
      width: 100%;
      min-height: 84px;
      display: flex;
      position: relative;

      @media screen and (max-width: 768px) {
        min-height: 36px;
        max-height: auto;
      }
      
      .category-resp-container {
        min-height: inherit;
      }
      
      .item {
        font-weight: $font-weight-normal;
        color: $color-body;

        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        box-sizing: border-box;
      }
      
      @media screen and (min-width: 769px) {
        .item-border {
          &:after {
            content: '';
            position: absolute;
            left: 0;
            height: 100%;
            width: 1px;
            background-color: $color-line;
          }
        }
      }

      @media screen and (max-width: 768px) {
        .item {
          &:after {
            content: '';
            position: absolute;
            right: -1px;
            height: 50%;
            width: 1px;
            background-color: $color-line;
          }
        }

        .item:nth-last-of-type(1) {
          &:after {
            content: none;
          }
        }
      }

      .item-desc {
        padding: 0 30px;
        font-size: $font-size-smallest;
        text-align: left;

        display: flex;
        align-items: flex-start;
        justify-content: center;
        flex-direction: column;

        p {
          margin: 0;
        }
        .title {
          color: $color-title;
          font-weight: $font-weight-semibold;
        }
        .desc {
          color: $color-caption;
        }
        .title-desc {
          padding-top: 5px;
          font-size: $font-size-tiny * 0.9;
        }
      }
      

      .item {
        font-size: $font-size-small;
        font-weight: $font-weight-semibold;
        text-align: center;
        padding: 0 30px;

        @media screen and (max-width: 768px) {
          font-size: $font-size-tiny;
        }

        @media screen and (max-width: 560px) {
          padding: 10px;
        }

        .tick {
          filter: $color-filter-body;
        }

        .desc {
          font-size: $font-size-tiny;
          font-weight: $font-weight-normal;
          color: $color-caption;
          margin: 0;
        }
      }

      .border-left {
        border-left: solid 1px $color-line;
      }

      .border-right {
        border-right: solid 1px $color-line;
      }
    }

    .container-border {
      box-sizing: border-box;
      border-right: solid 1px $color-line;

      @media screen and (min-width: 576px) {
        border-left: solid 1px $color-line;
      }
    }

    .container-border-top {
      border-top: solid 1px $color-line;
    }

    .container-border-bottom {
      border-bottom: solid 1px $color-line;
    }

    .white {
      background-color: white;
    }

    .green {
      background-color: $color-form;
    }

    .please-read {
      font-size: $font-size-smallest;
      font-weight: $font-weight-semibold;
      color: $color-body;
      margin: 0;

      @media screen and (max-width: 768px) {
        font-size: $font-size-tiny;
      }

      button {
        color: $color-hover;
        font-size: $font-size-smallest;
        font-weight: $font-weight-semibold;
        &:hover {
          color: $color-hover-alt;
        }

        @media screen and (max-width: 768px) {
          font-size: $font-size-tiny;
        }
      }
    }

    .no-padding {
      padding: 10px !important;
    }
  }

  .info-container {
    &:hover {
      .desc-resp-container,
      .item-container,
      .item-container .item-desc,
      .please-read {
        background-color: $color-caption;

        .item,
        .title,
        .desc,
        {
          color: white;
        }
      }

      .tick {
        filter: brightness(100%) saturate(100%) !important;
      }
    }
  }

  .info-accordian-container:nth-of-type(1),
  .info-container:nth-of-type(1) {
    border-top: solid 1px $color-line;
  }

  .temp-container {
    position: fixed;
    width: 100%;
    top: -1000px;
  }

  .tab-container {
    @media screen and (max-width: 560px) {
      padding: 10px 0;
      .input-wrapper-main {
        margin: 0;
      }
    }
    .dropdown {
      @media screen and (max-width: 768px) {
        input,
        .list-item p {
          font-size: $font-size-tiny;
          
        }
  
        .list-item {
          word-wrap: break-word;
        }
      }
    }
  }
}
